.button {
  .transition(100ms);
  display: block;
  padding: 1rem 1rem;
  text-align: center;
  font-size: 1.5rem;

  &:hover {
    .transition(100ms);
  }

  .variant(primary; {
    background-color: @primary;
    color: @primary_white;
    border: 1px solid @opacity_black_20;
    text-decoration: none;

    &:hover {
      background-color: @primary_white;
      color: @primary;
      border-color: @primary;
    }
  });

  .variant(block; {
    width: 100%;
  })
}